<template>
  <div class="container">
    <h1>Vue Filter</h1>
    <hr>
    <div class="row">
      <div class="col-md-4">
        <h2>currency:</h2>
        <input type="number" v-model.number="demoCurrency">
        <h3>{{ demoCurrency | currency }}</h3> <!-- 過濾貨幣 -->
      </div>

      <div class="col-md-4">
        <h2>lowercase:</h2>
        <input v-model="demoLowerCase">
        <h3>{{ demoLowerCase | lowercase }}</h3> <!-- 過濾貨幣 -->
      </div>

      <div class="col-md-4">
        <h2>Letter Grade:</h2>
        <input type="number" v-model.number="demoScore">
        <h3>{{ demoScore | letterGrade }}
          <small>GPA: ( {{ demoScore | gpa }} )</small> <!-- 過濾 GPA -->
        </h3> <!-- 過濾 Letter Grade -->
      </div>
    </div>

    <hr>

    <h3>學期分數: </h3>
    <table class="table">
      <thead>
        <tr>
          <th>name</th>
          <th>Letter Grade</th>
          <th>score</th>
          <th>GPA</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in scoreList" :key="index">
          <td>{{ item.name }}</td>
          <td>{{ item.score | letterGrade }}</td>
          <td>{{ item.score }}</td>
          <td>{{ item.score | gpa }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data() {
    return {
      demoCurrency: 1000,
      demoLowerCase: 'LLLLLOWER CASE',
      demoScore: 99,
      scoreList: [
        { name: 'Jacky', score: 100 },
        { name: 'Momo', score: 99 },
        { name: 'Jasper', score: 89 },
        { name: 'Eason', score: 80 },
        { name: 'Mars', score: 79 },
        { name: 'Mike', score: 70 },
        { name: 'Ben', score: 69 },
        { name: 'Aaron', score: 60 },
        { name: 'Mary', score: 59 },
        { name: 'Otis', score: 55 },
      ],
    };
  },
};
</script>



